@charset "utf-8";

/* 상단 lnb */
.header_inner > .top{
    width:1050px;
    height:37px;
}
.header_inner > .top > .h_lnb{
    width:300px;
    float:right;
}
.header_inner > .top > .h_lnb ul{
    display:flex;
    justify-content: right;
    padding:10px 0px;
}
.header_inner > .top > .h_lnb ul li{
    margin:0px 10px;
}
.header_inner > .top > .h_lnb ul li:last-child{
    transform:translateY(-4px);
}
.header_inner > .top > .h_lnb ul li::after{
    content: "|";
    margin-left:10px;
}
.header_inner > .top > .h_lnb ul li:last-child::after{
    content:none;
}
.header_inner > .top > .h_lnb ul li a{
    font-size:12px;
}
.top .material-icons{
    transform: translateY(4px);
    font-size:18px;
}

/* 중간부분 */
.header_inner > .middle{
    height:63px;
    width:1050px;
}
.header_inner > .middle > h1{
    width:82px;
    height:42px;
    display: inline-block;
    transform: translateY(12px);
}
.header_inner > .middle > .kully_mnu{
    width:200px;
    display: inline-block;
    margin-left:15px;
}
.header_inner > .middle > .kully_mnu > ul{
    display:flex;
}
.header_inner > .middle > .kully_mnu > ul li{
    margin-left:10px;
}
.header_inner > .middle > .kully_mnu > ul li::after{
    content:"|";
    margin-left:10px;
}
.header_inner > .middle > .kully_mnu > ul li:last-child::after{
    content:none;
}
.header_inner > .middle > .kully_mnu > ul li a{
    font-size:18px;
    font-weight:500;
    font-weight: bold;
    color:#ccc;
}
.header_inner > .middle > .kully_mnu > ul li a:hover, .header_inner > .middle > .kully_mnu > ul li:first-child a{
    color:#5F0080;
}

/* 서치박스 */
.search_box{
    width:390px;
    display:inline-block;
    position:relative;
    left:20px;
}
.search_box > #search{
    width:390px;
    height:48px;
    border:1px solid #5F0080;
    border-radius:6px;
    padding-left:10px;
}
.search_box >.search_icon{
    color:#5F0080;
    font-size:28px;
    position:absolute;
    top:10px;
    right:5px;
}

.middle > .i_lnb{
    float:right;
    width:200px;
    margin-right:10px;
    transform: translateY(-45px);
}
.middle > .i_lnb > ul{
    display:flex;
    justify-content: right;
}
.middle > .i_lnb > ul li{
    margin-left:26px;
}
.middle > .i_lnb .material-icons{
    font-size:34px;
}
.middle > .i_lnb .material-icons:hover{
    color:#5F0080;
}

/* 하단부분 */
.bottom{
    height:56px;
    width:1050px;
    padding-top:10px;
}
.bottom > .mnu{
    width:200px;
    font-size:16px;
    cursor:pointer;
}
.bottom > .mnu:hover{
    color:#5F0080;
}
.bottom > .mnu > .material-icons{
    font-size:24px;
    margin-right:4px;
    transform: translateY(5px);
}
.bottom > .gnb{
    display:inline-block;
    margin-left:130px;
}
.bottom > .gnb >ul{
    width:600px;
    display:flex;
    justify-content: space-around;
}
.bottom > .gnb >ul li a{
    font-size:16px;
    font-weight:bold;
}
.bottom > .gnb >ul li a:hover{
    color:#5F0080;
    border-bottom:1px solid #5F0080;
}
