@charset "utf-8";

.f_lnb{
    width:520px;
    position:absolute;
    top:0px;right:0px;
}
.f_lnb  ul{
    display:flex;
    justify-content: space-around;
}
.f_lnb  ul li a{
    font-size:14px;
}
.f_lnb  ul li .bold{
    font-weight:bold;
}
.f_title{
    font-size:20px;
    padding-bottom:20px;
}
.time{font-size:16px;}
.time > .f_bold{
    font-weight:bold;
    font-size:28px;
}
address{
    font-style:normal;
    color:#999;
    position:absolute;
    top:40px;right:80px;
    line-height: 18px;
}
.top_btn{
    padding:16px 12px;
    background:#5F0080;
    color:#fff;
    font-size:16px;
    border:1px solid #5F0080;
    border-radius:12px;
    position:fixed;
    bottom:50px;right:40px;
    cursor:pointer;
    transition:0.4s;
}
.top_btn:hover{
    background:#FFF;
    color:#5F0080;
}