@charset "utf-8";

/* 슬라이드 */
main{overflow: hidden;}
.slide_wrap{
    width:100%;
    height:370px;
    /* background:#ff0; */
    position:relative;
}
.slide_wrap > h2{display:none;}
.slide_wrap img{
    width:1900px;
    min-width:1050px;
    height:370px;
    object-fit: cover;
}
.slide_wrap .slide{
    width:7600px;
    display: flex;
    position:absolute;
    transition:0.4s;
}
.slide_wrap .slide li {
    width:1900px;
}
.slide_wrap .slide:hover + .btn_wrap{
    opacity: 1;
}
.btn_wrap:hover{opacity:1;}
.btn_wrap{
    width:1050px;
    margin:0px auto;
    position:relative;
    opacity: 0;
    transition: 0.4s;
}
.right_btn,.left_btn{
    position:absolute;
    top:165px;
    font-size: 60px !important;
    width:45px;
    line-height:50px;
    background-color: rgb(0, 0, 0,.4);
    border-radius:50%;
    color:#fff;
    cursor:pointer;
}
.right_btn{
    right:-90px;
    padding:0px 12px 0px 5px;
}
.left_btn{
    left:-90px;
    padding:0px 16px 0px 0px;
    
}
