/* 이상품어때요 */
.product{margin-top:40px;}
.m_product{
    width:1050px;
    height:568px;
    margin:0px auto;
    display:flex;
}
.m_product .img_box{
    width:249px;
    height:320px;
    overflow: hidden;
}
.m_product li:hover img{
    transform: scale(1.03);
}
.m_product li{
    height:434px;
    margin:0px 10px;
    position:relative;
}
.m_product li img{
    width:249px;
    height:320px;
    transition: 0.5s;
}
.m_product li .icon{
    width:45px;
    height:45px;
    position:absolute;
    bottom:130px;
    right:15px;
}
article > h3{
    text-align:center;
    padding:30px 0px;
    font-size: 28px;
}
.m_product .title{
    font-size:16px;
    width:249px;
    display: block;
    padding:10px 0px;
    line-height:22px;
}
.m_product .text{
    font-size:16px;
    font-weight: bold;
    padding:10px 0px;
}
.m_product .text .color_orange{
    color:#fa622f;
    padding-right:4px;
}
.m_product .Fprice{
    font-size:14px;
    color:#b5b5b5;
}
.text_line {color:#b5b5b5;}
.second_list li:first-child .icon{display: none;}
/* 배너 */
.banner{
    width:1050px;
    height:220px;
    margin:0px auto;
}
.banner img{
    padding:40px 0px;
    width:1050px;
    height:140px;
}

/* notfound페이지 */
