
.title01{
    text-align: center;
    transform: translateX(20px);
    font-size:20px;
    padding:20px 0px;
}
.login_box{
    width:300px;
    text-align: center;
    margin:100px auto;
}
.find{
    display:flex;
    justify-content:right;
    list-style:none;
    transform: translateX(50px);
}
.find >li{
    padding-left:10px;
}
.find >li::after{
    content:'|';
    margin-left:10px;
}
.find >li:last-child:after{
    display:none;
}
.find >li >a{
    text-decoration:none;
    color:#333;
    font-size:14px;
    margin:10px 0px;
}
#id,#pw{
    width:340px;
    line-height:50px;
    font-size:16px;
    margin-bottom:15px;
    border:1px solid #ccc;
    border-radius:4px;
}
.login_btn, .join_btn{
    width:350px;
    line-height:50px;
    margin-bottom:10px;
    font-size:16px;
    border:1px solid #5F0080;
    border-radius:4px;
    cursor:pointer;
}
.login_btn{
    margin-top:10px;
    background:#5F0080;
    color:#fff;
}
.join_btn{
    background:#fff;
    color:#5F0080
}